import { createElement } from '@/utils/createElement';
import st from '@/styles/part.scss';

export function createPart(bgImage, sizes, text = 'Пусто', vertical) {
  const part = createElement('div', st.part);
  const bg = createElement('div', st.bg);
  const txt = createElement('h2', [st.text, vertical ? st.vertical : ''].join(' '));
  part.style.height = sizes[1] + 'px';
  part.style.width = sizes[0] + 'px';
  txt.innerHTML = text;
  bg.innerHTML = bgImage;
  part.appendChild(bg);
  part.appendChild(txt);
  initListener(part);
  return part;
}
function initListener(elem) {
  const listener = (e) => {
    e.target.classList.toggle(st.active);
  };
  elem.addEventListener('click', listener, true);
}
